import React, { useCallback } from 'react';
import {
  Box, HStack, Image, List, ListItem, Text
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import style from './AccountType.module.css';
import { useAppSelector } from '../../../../hooks/store';
import Icon from '../../../../components/Icon';

const SetupWallet = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const logo = useAppSelector((store) => store.core.programConfig?.brand.landscapeLogo, shallowEqual);

  const onPress = useCallback((accountType: 'personal' | 'business') => {
    navigate(accountType === 'personal' ? '/ao/account/setup-options' : '/ao/kyb/businessDetails');
  }, []);

  return (
    <Box className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
      }}
      >
        <MiniBox
          varient="box"
          showBack={false}
        >
          <Box width="100%" paddingTop="15px">
            {logo
              ? (
                <Box>
                  <Image maxWidth="250px" margin="90px auto 46px auto" src={logo} />
                </Box>
              ) : null }

            <Box>
              <Text className="text-light" variant="regular" textAlign="center" marginBottom="40px">
                {t('account_type.desc')}
              </Text>
              <Text fontWeight="600" variant="regular" textAlign="center" color="#000000" marginBottom="40px">
                {t('account_type.option_title')}
              </Text>
              <List>
                <ListItem>
                  <HStack className={style.actionBox} onClick={() => onPress('personal')}>
                    <Text variant="regular">{t('account_type.option1')}</Text>
                    <Icon icon="Arrow-small-right" size={12} />
                  </HStack>
                  <HStack className={style.actionBox} onClick={() => onPress('business')}>
                    <Text variant="regular">{t('account_type.option2')}</Text>
                    <Icon icon="Arrow-small-right" size={12} />
                  </HStack>
                </ListItem>
              </List>
            </Box>
          </Box>
        </MiniBox>
      </form>
    </Box>
  );
};

export default SetupWallet;
